.nav {
  margin-right: 50px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    list-style-type: none;
    padding: 0;
    margin: 0;
    height: 60px;
  }

  &__link {
    display: flex;
    text-transform: uppercase;
    color: $dark-gray-color;
    text-decoration: none;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 60px;
  }

  &__link:hover {
    color: $blue-color;
  }

  &__item {
    margin-right: 20px;
  }

  &__item:last-child {
    margin-right: 0;
  }
}

.is-active {
  position: relative;
  color: $blue-color;
}

.is-active::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 4px;
  background: $blue-color;
  border-radius: 2px;
  left: 0;
  bottom: 0;
}
