.card {
  display: flex;
  width: 200px;
  flex-direction: column;
  padding: 32px 16px 16px;
  background: $white-color;
  border: 1px solid $border-color;
  border-radius: 5px;
  box-sizing: border-box;

  &__img {
    width: 160px;
    height: 134px;
    margin: 0 auto;
  }

  &__title {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: $dark-gray-color;
    padding-top: 40px;
  }

  &__product-code {
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: $light-gray-color;
    padding-top: 4px;
  }

  &__stars-and-reviews {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 16px;
  }

  &__reviews {
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    color: $dark-gray-color;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 23px;
  }

  &__price-text {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    color: $light-gray-color;
  }

  &__price-value {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    color: $dark-gray-color;
  }

  &__buy-link {
    padding-top: 16px;
    text-align: center;
  }

  &__buy-link-text {
    height: 38px;
    line-height: 38px;
    display: block;
    background: $blue-color;
    border: 1px solid $blue-color;
    border-radius: 5px;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;
    text-decoration: none;
    color: $white-color;
  }

  &__buy-link-text:hover {
    background: $white-color;
    color: $blue-color;
    border: 1px solid $blue-color;
    border-radius: 5px;
  }
}
